if (document.querySelector('.input-check__input')) {
	var inputCheckbox = document.querySelectorAll('.input-check__input');
	for (var i = 0; i < inputCheckbox.length; i++) {
		inputCheckbox[i].addEventListener('click', function(){
			this.classList.toggle('input-check__input--active');
		})
	}
}

if (document.querySelector('.mobile-menu-not-registered__open')) {
	document.querySelector('.mobile-menu-not-registered__open').addEventListener('click', function(){
		document.querySelector('.mobile-menu-not-registered').classList.toggle('d-block');
		this.classList.toggle('mobile-menu-header__hum--active');
		document.querySelector('body').classList.toggle('hidden');
	})
}

if (document.querySelector('.sidebar__open')) {
	document.querySelector('.sidebar__open').addEventListener('click', function(){
		document.querySelector('.sidebar').classList.toggle('d-block');
		this.classList.toggle('mobile-menu-header__hum--active');
		document.querySelector('body').classList.toggle('hidden');
	})
}

if (document.querySelector('.consult-item__decr-open')) {
	var openMore = document.querySelectorAll('.consult-item__decr-open');
	for (var i = 0; i < openMore.length; i++) {
		openMore[i].addEventListener('click', function(){
			this.parentElement.parentElement.querySelector('.consult-item__decr').style.height = "auto";
			this.style.display = "none";
			this.parentElement.querySelector('.consult-item__decr-hide').style.display = "block";
		});
	}
	var closeMore = document.querySelectorAll('.consult-item__decr-hide');
	for (var i = 0; i < closeMore.length; i++) {
		closeMore[i].addEventListener('click', function(){
			this.parentElement.parentElement.querySelector('.consult-item__decr').style.height = "30px";
			this.style.display = "none";
			this.parentElement.querySelector('.consult-item__decr-open').style.display = "block";
		})
	}
}

if (window.matchMedia('(max-width: 700px)').matches) {
  if (document.querySelector('.consult-item__title-btn')) {
  	var citb = document.querySelectorAll('.consult-item__title-btn');
  	for (var i = 0; i < citb.length; i++) {
  		citb[i].addEventListener('click', function(){
  			this.parentElement.classList.add('consult-item__title-info--active');
  			this.parentElement.querySelector('.consult-item__title-info').style.display = "block";
  			this.parentElement.querySelector('.consult-item__title-info').style.opacity = "1";
  			this.parentElement.querySelector('.consult-item__title-info').style.zIndex = "12";
  			document.querySelector('.overlay').style.display = "block";
  		});
  	}
  	document.querySelector('.overlay').addEventListener('click', function(){
  		document.querySelector('.consult-item__title-info--active').querySelector('.consult-item__title-info').style.display = "none";
			document.querySelector('.consult-item__title-info--active').querySelector('.consult-item__title-info').style.opacity = "0";
			document.querySelector('.consult-item__title-info--active').querySelector('.consult-item__title-info').style.zIndex = "0";
			document.querySelector('.consult-item__title-info--active').classList.remove('consult-item__title-info--active');
			document.querySelector('.overlay').style.display = "none";
  	});
  }
} 